import React from 'react';
import { Link } from 'react-router-dom';
import EntityContainer from 'modules/entity/containers';
import Helmet from 'react-helmet';
import Typography from '@material-ui/core/Typography';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import { ErrorBoundary, Loader } from 'shared/components';
import Skeleton from '@material-ui/lab/Skeleton';
import { HtmlParser } from 'shared/components';
import { useAppState } from 'shared/state';
import { useRouter } from 'shared/hooks';
import { NewsTemplateType } from 'shared/typings/news.types';

// eslint-disable-next-line no-unused-vars
const TemplatesView = () => {
	const { companyId } = useAppState();
	const { params } = useRouter();

	const templateId = parseInt((params as { templateId: string })?.templateId);

	return (
		<EntityContainer.One
			id={templateId}
			entity="templates"
			name={`SingleTemplate-${templateId}`}
			url={`${companyId}/notification-templates/${templateId}`}
			params={{}}
			relations=""
		>
			{({ item, isFetched }) => {
				const templateItem = item as NewsTemplateType;
				return (
					<div className="news-page mb-2.5 py-6">
						<Helmet>
							<title>KENNEKT | {`${templateItem.title}`}</title>
							<meta name="description" content="KENNEKT Announcement" />
						</Helmet>
						<div className="flex-center-between mb-4">
							{isFetched ? (
								<h1 className="text-2xl font-bold dark:text-white">{templateItem.title}</h1>
							) : (
								<div className="w-[200px]">
									<Skeleton />
								</div>
							)}
						</div>
						<div className="news-breadcrumb mb-5">
							{isFetched ? (
								<Breadcrumbs aria-label="breadcrumb">
									<Link color="inherit" to="/news/templates">
										<span className="text-primary transition hover:text-primary-dark">
											All Templates
										</span>
									</Link>
									<Typography color="textPrimary">{templateItem.title}</Typography>
								</Breadcrumbs>
							) : (
								<div className="w-[200px]">
									<Skeleton />
								</div>
							)}
						</div>
						<div className="uni-page__content dark:text-white">
							<Typography variant="h6" gutterBottom>
								Template content
							</Typography>
							<div className="mb-5 p-5">
								{isFetched ? (
									<>
										{/* @ts-ignore */}
										<HtmlParser text={templateItem.body} />
									</>
								) : (
									<Loader tiled withOverlay />
								)}
							</div>
						</div>
					</div>
				);
			}}
		</EntityContainer.One>
	);
};

export default function TemplatesViewWithErrorBoundary() {
	return (
		<ErrorBoundary>
			<TemplatesView />
		</ErrorBoundary>
	);
}
